	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"Our Mission","picture":null,"pictureCaption":null},"html":"<p>No Smile Left Behind’s mission is to overcome barriers to care to promote oral health and deliver compassionate dental care for one and all.</p>\n<p>No Smile Left Behind is a state-of-the-art, self-contained dental office on wheels capable of meeting the comprehensive treatment needs of patients right where the patients happen to already be. Whether treatment is needed at schools, health fairs, summer camps, housing communities, private businesses, or other venues, No Smile Left Behind can help!</p>\n<p>No Smile Left Behind can save time, money, and hassle by arriving at locations which are already hosting the patients at the time of service. We provide convenient &#x26; excellent on-site dental care without requiring anyone to make an appointment, take time off at work, or juggle all the other demands of life just to make it to the dental office.</p>\n<p>No matter what may make getting quality dental care difficult or inconvenient, No Smile Left Behind is happy to deliver compassionate care right where it’s needed.</p>"}},{"node":{"frontmatter":{"title":"Dr. Nathan Becker","picture":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--1ImzCrub--/q_jpegmini/v1495031583/Nathan_geager.jpg","pictureCaption":"Nathan Becker, DDS"},"html":"<p>Dr. Nathan has spent his entire career in mobile dentistry providing compassionate care for the underserved of all ages, with a focus on children. As a result, he has developed a unique skill set and sensitivity specially tailored to achieving excellent results in prevention, education, diagnosis, &#x26; comprehensive restorative treatment among those who have difficulties accessing regular care for any reason.</p>\n<p>In his free time, Dr. Nathan enjoys reading, rock climbing, piano improvisation, cooking, meditation, and spending time outdoors.</p>"}},{"node":{"frontmatter":{"title":"The Mobile Office","picture":null,"pictureCaption":null},"html":"<p>No Smile Left Behind's mobile office is under construction, but the finished office will look almost identical to this:</p>\n<p><label><em>The Mobile Office</em></label>\n<img\n      height=\"100%\" width=\"100%\"\n     src=\"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--aZ3nUM6E--/q_jpegmini/v1495027575/No_Smile_Left_Behind_1_gkyf3h.jpg\">\n</br>\n<label><em>Restorative Chair</em></label>\n<img\n    height=\"100%\" width=\"100%\"\n    src=\"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--wEIxIMV4--/q_jpegmini/v1495027574/Front_Treatment_Room_1_rcdjfv.jpg\">\n</br>\n<label><em>Hygiene chair</em></label>\n<img\n    height=\"100%\" width=\"100%\"\n     src=\"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--Aht5IyUV--/q_jpegmini/v1495027573/Rear_Treatment_Room_wkgqfm.jpg\"></p>"}}]}},"pathContext":{}}

